<div class="filter"><div class="title"><span class="span1">数据筛选</span></div><a-form class="formout" layout="inline"><div class="forms"><a-form-item label="输入查询:"><a-input placeholder="用户名称" v-model:value="searchForm.mixedName" style="width: 100px"></a-input></a-form-item><a-form-item label="输入查询:"><a-input placeholder="案例编号/案例名称" v-model:value="searchForm.mainTitle"  style="width: 140px"></a-input></a-form-item><a-form-item label="状态:"><a-select v-model:value="searchForm.statusStr" placeholder="全部" style="width: 160px" allowClear><a-select-option value="待学习">待学习</a-select-option><a-select-option value="学习中">学习中</a-select-option><a-select-option value="已完成">已完成</a-select-option><a-select-option value="异常">异常</a-select-option></a-select></a-form-item><a-form-item label="课程分类:"><a-select v-model:value="searchForm.seniorCategory" placeholder="全部" style="width: 150px" allowClear><a-select-option v-for="item in sort" :value="item.id">{{ item.categoryName }}</a-select-option></a-select></a-form-item><a-form-item label="时间:" style="width: 50%"><a-range-picker v-model:value="searchForm.time" showTime></a-range-picker></a-form-item></div><a-form-item class="btns"><a-button type="primary" @click="submitForm">查询</a-button><a-button @click="resetForm">重置</a-button></a-form-item></a-form></div><div class="list"><a-tabs><a-tab-pane tab="数据列表"><a-table :columns="columns" :data-source="data" :pagination="pagination" :loading="queryRecordLoding" :row-selection="{ selectedRowKeys: selectedRowKeys, onChange: onSelectChange }" rowKey="id" @change="pageChange"><template v-slot:equi="{ text, record }">{{ record.duration > 0 && record.appDuration > 0 ? "PC,APP" : record.duration > 0 ? "PC" : record.appDuration > 0 ? "APP" : " " }}</template><template v-slot:badge="{ text, record }"><a-badge v-if="record.abnormalMark == true" color="cyan" text="异常"></a-badge><a-badge v-else-if="record.studyStatus == 1" color="orange" text="学习中"></a-badge><a-badge v-else-if="record.studyStatus == 0" color="yellow" text="待学习"></a-badge><a-badge v-else-if="record.studyStatus == 2" color="purple" text="已完成"></a-badge></template><template v-slot:action="{ text, record }"><div class="option"><div class="option-container" @click="showViewrecord(record)"><a>查看</a></div><div class="option-container" @click="delrecord(record)"><span>重置记录</span></div></div></template></a-table></a-tab-pane><template v-slot:tabBarExtraContent><Abnormal></Abnormal></template></a-tabs><div class="table-foot"><div class="all-check btn-other"><a-checkbox :checked="checked.check" @change="onSelectAll">全选</a-checkbox></div><div class="all-check btn-other"><a-checkbox :checked="checkedother.check" @change="selectOther">反选</a-checkbox></div><a-select class="more-btn btn-other" placeholder="批量操作" style="width: 120px"><a-select-option @click="closeCheckedRecords" value="closeCheckedRecords">重置</a-select-option></a-select></div><a-modal :visible="viewrecord.show" title="查看记录" dialogClass="modal-help" :width="500" :footer="null" @cancel="closeViewrecord"><a-form layout="vertical" :label-col="labelCol"><a-form-item label="加入学习:">{{ records.startDate }}</a-form-item><a-form-item label="访问课时页:">{{ records.startPeriodDate }}</a-form-item><a-form-item label="完成学习:">{{ records.doneDate || "未完成" }}</a-form-item><a-form-item label="学习进度:"><div class="card"><div class="left"><a-avatar shape="square" :size="74" :src="records.userCover"></a-avatar></div><div class="right"><div class="top"><span class="name">{{ records.username }}</span></div><div class="bottom"><div class="up"><span>学习进度</span></div><div class="down"><a-progress :percent="records.studyProgress"></a-progress></div></div></div></div></a-form-item></a-form></a-modal></div>